import {
    Table,
    Thead,
    Tbody,
    Tfoot,
    Tr,
    Th,
    Td,
    TableCaption,
    TableContainer,
    Checkbox,
    CheckboxGroup
} from '@chakra-ui/react'
import { useEffect, useState } from 'react';

interface IStudyProps {
    "modality": string;
    "imagecnt": number;
    "studydate": string;
    "studydesc": string;
    "reportstatus": number;
    "seriescnt": number;
    "verifyflag": number;
    "studykey": number;
    "patientkey": string;
    "pid": string;
    "pname": string;
}

export default function StudyList() {
    // Fetching
    const [page, setPage] = useState(1);
    const [isLoading, setIsLoading] = useState(true);
    const [studies, setStudies] = useState([]);
    const fetchStudies = async() => {
        const response = await fetch(`https://192.168.30.88:8443/v1/api/pacs/studies?page=${page}`);
        const json = await response.json();
        setStudies(json);
        setIsLoading(false);
    }
    useEffect(() => {
        fetchStudies();
    }, []);

    return (
        <TableContainer padding={'23px'}>
            <Table variant='striped' colorScheme='blue' fontSize={'sm'}>
                <TableCaption color={'gray.300'}>MyPACS Study list @PACSPLUS-DATABASE</TableCaption>
                <Thead bg={'blue.500'}>
                    <Tr>
                        <Th color='white'> </Th>
                        <Th color='white'> </Th>
                        <Th color='white'> </Th>
                        <Th color='white'> </Th>
                        <Th color='white'> </Th>
                        <Th color='white'> </Th>
                        <Th color='white'></Th>
                        <Th color='white'></Th>
                        <Th color='white'>Verify</Th>
                    </Tr>
                </Thead>
                <Tbody>
                    <CheckboxGroup>
                        {studies.map((study: IStudyProps) => (
                            <Tr key={study.studykey}>
                                <Td><Checkbox value={study.studykey} />{study.pid}</Td>
                                <Td>{study.pname}</Td>
                                <Td>{study.modality}</Td>
                                <Td>{study.studydesc}</Td>
                                <Td>{study.studydate}</Td>
                                <Td>{study.reportstatus}</Td>
                                <Td>{study.seriescnt}</Td>
                                <Td>{study.imagecnt}</Td>
                                <Td>{toStringReportStatus(study.reportstatus)}</Td>
                            </Tr>
                        ))}
                    </CheckboxGroup>
                </Tbody>
            </Table>
        </TableContainer >
    );
}

function toStringReportStatus(status:number) {
    switch(status) {
        case 3 :
            return "";
        case 4 :
            return "";
        case 5 :
            return "";
        case 6 :
            return "";
    }
}